@charset "utf-8";
/*gong zuo dong tai scss file*/
.gongzuodongtai {
  width: 420px;
  height: 298px;
  overflow: hidden;
  float: right;
  .box-hd {
    width: 100%;
    height: 35px;
    line-height: 35px;
    border-bottom: 1px solid $base_theme_color;
    span.title {
      font-size: 1.4*$base_font_size;
      color: $base_theme_color;
      //font-weight: bold;
    }
    a.more {
      float: right;
      text-decoration: none;
      color: $base_theme_color;
      font-size: 1.2*$base_font_size;
    }
  }

  .box-bd {
    padding-top: 10px;
    a {
      text-decoration: none;
    }
    /*    ul > li:first-child {
          padding: 8px 0;
          margin-bottom: 11px;
          border-bottom: 1px solid darken(#fff, 25%);
          a h3 {
            padding: 5px 0;
            font-size: 1.4*$base_font_size;
            color: #000;
          }
          a p {
            font-size: 1.1*$base_font_size;
            color: $base_font_color;
            height: 40px;
            line-height: 20px;
            overflow: hidden;
          }
        }*/
    /*    ul > li:not(:first-child) {*/
    ul > li{
      height: 32px;
      line-height: 32px;
      border-bottom: 1px dashed darken(#fff, 10%);
      a {
        width: 330px;
        color: $base_theme_color;
        font-size: 1.25*$base_font_size;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
      }
      span {
        float: right;
      }
    }
  }
}
