@charset "utf-8";
/* ban shi da ting scss file*/
.banshidating {
  float: right;
  width: 285px;
  height: 157px;
  margin-bottom: 15px;
  //box-shadow: 1px 1px 1px #888888;
  outline:1px solid #e5e5e5;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  .box-hd {
    height: 35px;
    line-height: 35px;
    margin: 0 auto;
    background: $base_theme_color;
    color: #fff;
    font-size: 1.4*$base_font_size;
    text-align: center;
  }
  .box-bd {
    background: #fff;
    padding: 20px 15px;
    a {
      text-decoration: none;
      display: inline-block;
      width: 61px;
      height: 81px;
      margin-right: 10px;
      margin-left: 10px;
      img {
        width: 61px;
        height: 61px;
      }
      h3 {
        text-align: center;
        font-size: 1.2*$base_font-size;
        color: $base_theme_color;
      }
    }

  }
}
